.label-md {
    font-family: 'Oxanium', 'sans-serif';
    display: inline;
    color: rgba(0, 0, 0, 0.850);
    vertical-align: middle;
    font-size: 15px;
    font-weight: bold;
  //  background-color: rgb(251, 255, 0);
    padding: 10px;
    border-radius: 3px;
    //border: 1px solid rgb(141, 132, 0);
    background-color: #e4e4e4;
    margin: 5px;
    flex-grow: 1;
    text-align: center;
    max-width: 200px;
    cursor: pointer;
}

.labels {
    display: flex;
    flex-wrap: wrap;
}

.works-label {
    margin: 20px 5px;
    h2 {
        letter-spacing: 5px;
        color: rgba(0, 0, 0, 0.850);
    }
}

.active {
    background-color: rgb(255, 238, 0);
    animation: bounce 1s ease-in-out;
}

.filter-wrapper {
    width: 100%;
}

.item {
    display: flex;
    flex-direction: column;
    padding: 0px 20px;
    margin: 20px 0px;
    animation: test 1s ease-in-out;
    width: 100%;
    .title {
        display: flex;
        align-items: center;
    }
    .group {
        display: flex;
        justify-content: space-between;
    }
    .description {
        flex-grow: 1;
        display: flex;
        align-items: center;
        p {
            height: 100%;
        }
    }
    .button-link {
        display: flex;
        justify-content: flex-end;
        .custom {
            color: #fff;
            color: rgb(220, 220, 220);
            border-radius: 3px;
            background-color: rgba(60, 60, 60, 0.85);
            padding: 10px;
        }
        a {
            svg {
                color: rgba(60, 60, 60, 0.85);
            }
        }
    }
}

@keyframes test {
    from {
    opacity: 0;
    };
    to {
        opacity: 1;
    }
}

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {transform: scale(1);}
    40% {transform: scale(1.3);} 
    60% {transform: scale(1.1);} 
}